* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, #root, .App {
    height: 100vh;
    width: 100vw;
    font-family: sans-serif;
}
.flex {
    display: flex;
}

.center {
    justify-content: center;
    align-items: center;
}

.column {
    flex-flow: column;
}

.clickable {
    cursor: pointer;
}
/*
Theme Colors
White: #ffffff
Pink: #ffccc2
Lavender: #cbcdf7
Purple: #7376ec
Gray: #595959
*/

h1, h2, h3, h4, h5, h6 {
    font-family: 'Raleway';
    text-transform: uppercase;
    font-weight: bold;
    color: #7376ec /*#7d7dff*/;
}

div, p, button {
    font-family: 'Tajawal';
    color: #595959 /*#7677ca*/;
}
/*
Pink: #ffd2c
Logo circle back: #d8d6fe
Lines: #6d68b8
“White”: #fff9f9
Text (everything except body & club name): #7d7dff
Body text: #7677ca
*/

header {
    height: 18%;
    margin-bottom: 5px;
}

.title {
    height: 60%;
    margin-bottom: calc(5vh * 0.18);
}

.nav {
    height: 35%;
    background: #ffccc2;
    justify-content: space-evenly;
}

.navpage {
    height: 100%;
    width: 25%;
    border-left: 1px solid #7376ec;
    border-right: 1px solid #7376ec;
}

.active {
    background: #cbcdf7;
}

h1 {
    font-size: 5vh;
}

h2 {
    font-size: 4vh;
}

button, p {
    font-size: 2.5vh;
}

button {
    background: none;
    border: none;
}

article {
    min-height: calc(82% - 5px);
    padding: 25px;
}

.content * {
    margin-bottom: 10px;
}

.heading {
    font-weight: bold;
    text-decoration: underline;
}

.footnote {
    font-weight: bold;
    font-style: italic;
}

.project {
    height: -webkit-max-content;
    height: max-content;
}

.project * {
    margin-bottom: 0;
}

.project p {
    display: inline-block;
    margin-bottom: 5px;
}

.float-r {
    margin-right: 15px;
}

.source {
    height: 30vh;
}

.source * {
    height: 100%;
    border: 1px solid black;
}

.contact {
    height: 1010px;
    width: 100%;
}

.contact iframe {
    height: 100%;
    width: 100%;
    border: 0px solid black;
}
@media (orientation: portrait) {
    h1 {
        font-size: 7vw;
    }

    a, p {
        font-size: 5vw;
    }

    article {
        padding: 30px;
    }
}

@media (orientation: portrait) and (min-width: 500px) {
    h1 {
        font-size: 5.75vw;
    }

    header {
        height: 12%;
    }

    article {
        min-height: 88%;
    }

    .title {
        height: 45%
    }

    .nav {
        height: 50%;
    }
}

@media (orientation: portrait) and (min-width: 600px) {
    header {
        height: 20%;
    }

    article {
        min-height: 80%;
    }

    .title {
        height: 60%
    }

    .nav {
        height: 35%;
    }
}

@media (orientation: portrait) and (min-width: 850px) {
    header {
        height: 11%;
    }

    .title {
        height: 60%;
        margin: 20px;
    }

    article {
        min-height: calc(89% - 20px);
    }
}

@media (orientation: landscape) and (max-height: 700px) {
    header {
        height: 22%;
    }

    article {
        min-height: 78%;
    }
}

@media (orientation: landscape) and (max-height: 600px) {
    h1 {
        font-size: 5.5vh;
    }

    a, p {
        font-size: 3.5vh;
    }
}

@media (orientation: landscape) and (max-height: 500px) {
    a, p {
        font-size: 4vh;
    }
}

@media (orientation: landscape) and (max-height: 400px) {
    header {
        height: 24%;
    }

    article {
        min-height: 76%;
    }

    h1 {
        font-size: 6vh;
    }

    a, p {
        font-size: 4.5vh;
    }
}
